header {
  background-color: #343a40;
  width: 100%;
  margin-bottom: 7px;
  box-shadow: 0 0 7px #004992; }

.navLink {
  color: #012f63;
  padding: 5px;
  border: solid #f2f2f2;
  border-width: 0 0 3px 0;
  margin-bottom: 0;
  margin-right: 1px; }

.navLink:hover {
  text-decoration: none;
  border: solid #016bc5;
  border-width: 0 0 3px 0; }

.navActivLink {
  text-decoration: none;
  color: #016bc5;
  border: solid #016bc5;
  border-width: 0 0 3px 0; }

.navBar {
  margin: 0 !important;
  padding: 0 !important;
  background-color: #f2f2f2; }

.toggler {
  margin: 2px auto; }

@media (max-width: 767px) {
  .navLink {
    border: solid transparent;
    text-align: center; }


  .navLink:hover {
    border: solid transparent;
    background-color: rgba(141, 155, 164, 0.16); }

  .navActivLink {
    border: solid transparent;
    background-color: rgba(141, 155, 164, 0.27); } }





