.headerTitle {
  color: #ffffff;
  margin: 5px 10px; }

.brandArea {
  display: flex; }

.navBrand {
  color: #ffffff;
  font-weight: bold;
  font-size: large;
  margin: auto; }

.navBrand:hover {
  color: #cecece;
  text-decoration: none; }

.line {
  background-color: #ffffff;
  opacity: 0.5;
  margin: 0;
  padding: 0;
  max-width: 450px;
  text-align: center;
  margin-left: auto;
  margin-right: auto; }

.infoBlock {
  font-size: 14px;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
  flex-wrap: wrap; }

